body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #CC3126;
  background: #fff;
  color: #FAFAFA;
  color: #000;
  /* max-width: 800px; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  padding-top: 2rem;
  margin: 0 auto;
  width: 500px;
  height: auto;
}

.edit {
  display: flex;
  /* justify-content: flex-end; */
  cursor: pointer;
  border: none;
}

.rm {
  /* background: red; */
  background: #FB4653;
  border-radius: 50%;
  /* color: #fff;
  color: #17181C; */
  border: none;
  cursor: pointer;
}

.done {
  text-decoration: line-through;
}

input {
  border-radius: 2px;
  background: #FFF;
}

.txt-input {
  width: 50%;
}

.txt-input:focus {
  border: #0e2f9d;
}

form {
  display: flex;
}

button {
  cursor: pointer;
}


/* loader */
.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}

.lds-dual-ring:after {
  content: "";
  display: block;
  width: 64px;
  height: 64px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid blue;
  border-color: blue transparent blue transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}


/* theme */
:root {
  --background: white;
  --text-primary: black;
  --text-secondary: royalblue;
  --accent: purple;
}
[data-theme='dark'] {
  --background: black;
  --text-primary: white;
  --text-secondary: grey;
  --accent: darkred;
}

.body-color {
  color: var(--text-primary);
  background-color: var(--background);
  font-size: large;
  font-weight: bold;
  padding: 20px;
  /* height: calc(100vh - 30px); */
  display: block;
  height: 100%;
  /* height: 100vh; */
  transition: all .5s;
}
button {
  color: var(--text-primary);
  background-color: var(--background);
  border: 2px var(--text-primary) solid;
  float: right;
  transition: all .5s;
}